import React from "react";
import { Container, Row, Col, Image } from "react-bootstrap";
//import TrackVisibility from "react-on-screen";
import HeaderAnimation from "../Common/AnimationStyles/HeaderAnimation";
import aboutJmbpWebImg_1 from "../../Assets/Home/about-jmbp-web-img-1.jpg";
import aboutJmbpWebImg_2 from "../../Assets/Home/about-jmbp-web-img-3.jpg";
import aboutJmbpWebImg_3 from "../../Assets/Home/about-jmbp-web-img-2.jpg";

const AboutJMBPWeb = () => {
  return (
    <Container>
      <Row>
        <Col>
          <div className="header">
            <HeaderAnimation
              Header="About Jmbp Web Solutions"
              description="We Don’t Just Build Websites, We Build Your Online Business!"
            />
            {/* <h2>About Jmbp Web Solutions</h2>
                  <h5>
                    We Don’t Just Build Websites, We Build Your Online Business!
                  </h5>
              */}
          </div>
        </Col>
      </Row>
      <div className="divLine"></div>
      <Row>
        <Col md={6} lg={6} className="about-jmbp-web-para-1">
          Jmbp Web Solutions is a Web Development agency made up of extremely
          talented, highly trained and skilled professionals who work with you
          to create your online presence and are dedicated to meet the goals and
          guidelines to deliver your project.
        </Col>

        <Col md={6} lg={6} className="about-jmbp-web-para-2">
          We are a small team of experts who are willing to work your way to
          help you succeed online.
        </Col>
      </Row>
      <Row>
        <Col md={4} lg={4}>
          <Image
            src={aboutJmbpWebImg_1}
            fluid
            rounded
            className="about-jmbp-web-img"
          />
        </Col>
        <Col md={4} lg={4}>
          <Image
            src={aboutJmbpWebImg_2}
            fluid
            rounded
            className="about-jmbp-web-img"
          />
        </Col>
        <Col md={4} lg={4}>
          <Image
            src={aboutJmbpWebImg_3}
            fluid
            rounded
            className="about-jmbp-web-img"
          />
        </Col>
      </Row>
    </Container>
  );
};

export default AboutJMBPWeb;
