import React from "react";
import { Container, Row, Col, Image } from "react-bootstrap";
import HeaderAnimation from "../Common/AnimationStyles/HeaderAnimation";
import { Link } from "react-router-dom";
import jmbp_Web_Design from "../../Assets/ServicesPage/AllServices/jmbp_Web_Design.jpg";
import jmbp_Web_Development from "../../Assets/ServicesPage/AllServices/jmbp_Web_Development.jpg";
