import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { Grid } from "@material-ui/core";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: "flex",
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
}));

export default function WhyChooseUsTab() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <Grid container>
        <Grid xs={12} md={2} lg={2} item>
          <Tabs
            orientation="vertical"
            variant="scrollable"
            value={value}
            onChange={handleChange}
            aria-label="Vertical tabs example"
            className={classes.tabs}
          >
            <Tab label="Security" {...a11yProps(0)} />
            <Tab label="Trust" {...a11yProps(1)} />
            <Tab label="Leading" {...a11yProps(2)} />
            <Tab label="United Kingdom" {...a11yProps(3)} />
            <Tab label="Passion" {...a11yProps(4)} />
          </Tabs>
        </Grid>
        <Grid xs={12} md={10} lg={10} item>
          <TabPanel value={value} index={0}>
            Security is of the highest priorities for JMBP Web Solutions. Our UK
            Server provide the highest grade security and access, our servers
            run the highest level firewalls, and our sites are monitored 24/7
            for hack attempts and malware.
            <br />
            <br />
            Whats more – we’re that security conscious, we guarantee that your
            valuable data never leaves the shores of United Kingdom.
          </TabPanel>
          <TabPanel value={value} index={1}>
            <p>
              Trust is critical in an industry that seems to be full of fly by
              night agencies and self proclaimed gurus. Trust is high on our
              agenda with our clients and our supply partners.
            </p>
            <p>
              We provide full transparency at all levels, and ensure that the
              companies that we trust to partner with us have the high standards
              and ethics that we carry.
            </p>
          </TabPanel>
          <TabPanel value={value} index={2}>
            <p>
              Leading the way as a full service digital agency, we have a very
              distinct definition of full service that makes us very different.
            </p>
            <p>
              Full Service to us means to be able to provide clients with every
              piece of the digital puzzle. Its not just about delivering every
              service, it means providing the strategy, delivering it and
              providing the analysis to back it up.
            </p>
          </TabPanel>
          <TabPanel value={value} index={3}>
            <p>
              British we are – and we’re proud of it. Our internal staff team
              deliver all the products and services, and we provide 24/7
              Australian based technical support.
            </p>
            <p>
              Just another of those little things that makes us your logical
              choice.
            </p>
            <p>
              You can always pick up the phone and chat to us, no long wait
              times, and no one on the other end of the phone that you cant
              understand.
            </p>
            <p>
              In UK – we take our customer service seriously and are happy to
              showcase it to the world.
            </p>
          </TabPanel>
          <TabPanel value={value} index={4}>
            <p>
              Passion is the thing that drives us. Our passion for the digital
              industry and our passion to get the best results for our clients.
              It doesn’t matter about the size of your marketing budget to us –
              every client is important and we want the best for you.
            </p>
            <p>
              Our passion flows down the chain from our directors through to all
              of our long standing team members. They stay with us because they
              love it here, love what we do, and love helping our clients.
            </p>
          </TabPanel>
        </Grid>
      </Grid>
    </div>
  );
}
