import React from "react";
import { Container, Row, Col, Image } from "react-bootstrap";
import { TechnologyList } from "../../Database/TechnologyImagesDB";
//import TrackVisibility from "react-on-screen";
import HeaderAnimation from "../Common/AnimationStyles/HeaderAnimation";

const Technology = () => {
  return (
    <Container>
      <Row>
        <Col>
          <div className="header">
            <HeaderAnimation
              Header="We Work With All Modern Technologies"
              description="We work on all popular Web Technology and Frameworks, we have done small and
                    large web applications using different Frameworks, CMS & platforms as per
                    client needs. We suggest Frameworks and CMS to clients based upon
                    their requirements."
            />
            {/* <h2>We Work With All Modern Technologies</h2> 
                  <h5>
                    We work on all popular CMS and Frameworks, we have done small and
                    large web applications using different CMS and platforms as per
                    client needs. We suggest CMS and Frameworks to clients based upon
                    their requirements.
                  </h5>
              */}
          </div>
        </Col>
      </Row>
      <div className="divLine"></div>
      <Row>
        {TechnologyList.map((tech) => (
          <Col sm={2} md={2} lg={2} key={tech.id}>
            <Image src={tech.imgUrl} fluid className="technology-img" rounded />
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Technology;
