import React from "react";
import { Jumbotron as Jumbo, Container, Row, Col } from "react-bootstrap";
//import Typical from "react-typical";

const ServiceJumbotron = () => {
  return (
    <Jumbo fluid className="service-jumbo">
      <Container>
        <Row>
          <Col md={12}>
            <div className="service-jumboText" id="service-jumboText-font-1">
              <p>Our Services</p>
              <p id="service-jumboText-font-2">
                We offer a full and comprehensive end-to-end service,
                <br /> including all aspects of Technical IT and development
                services,
                <br /> through to web design and digital marketing
              </p>
            </div>
          </Col>
        </Row>
      </Container>
    </Jumbo>
  );
};

export default ServiceJumbotron;
