import React from "react";
// import styled, { keyframes } from "styled-components";
// import fadeInDown from "react-animations/lib/fade-in-down";
// import fadeInUp from "react-animations/lib/fade-in-up";
import ReactHtmlParser from "react-html-parser";
import ScrollAnimation from "react-animate-on-scroll";

const HeaderAnimation = ({ Header, description }) => {
  // const fadeInDownAnimation = keyframes`${fadeInDown}`;
  // const fadeInUpAnimation = keyframes`${fadeInUp}`;

  // const HeaderDiv = styled.div`
  //   animation: 1s ${fadeInDownAnimation} linear;
  // `;

  // const DescriptionDiv = styled.div`
  //   animation: 1s ${fadeInUpAnimation} linear;
  // `;
  return (
    <>
      {/* <HeaderDiv>
        <h2>{Header}</h2>
      </HeaderDiv>
      <DescriptionDiv>
        <h5>{ReactHtmlParser(description)}</h5>
      </DescriptionDiv> */}

      <ScrollAnimation
        animateIn="fadeInDown"
        initiallyVisible={true}
        duration={2}
        linear
      >
        <h2>{Header}</h2>
      </ScrollAnimation>
      <ScrollAnimation
        animateIn="fadeInUp"
        initiallyVisible={true}
        duration={2}
        linear
      >
        <h5>{ReactHtmlParser(description)}</h5>
      </ScrollAnimation>
    </>
  );
};

export default HeaderAnimation;
