import React from "react";
// import styled, { keyframes } from "styled-components";
// import flipInX from "react-animations/lib/flip-in-x";
import ScrollAnimation from "react-animate-on-scroll";

const BounceInAnimation = ({ children }) => {
  // const flipInXAnimat = keyframes`${flipInX}`;

  // const FlipInXDiv = styled.div`
  //   animation: 2s ${flipInXAnimat} linear;
  // `;
  return (
    <ScrollAnimation
      animateIn="bounceIn"
      initiallyVisible={true}
      duration={3}
      animateOnce={true}
    >
      {children}
    </ScrollAnimation>
  );
};

export default BounceInAnimation;
