import React from "react";
import { Nav, Navbar } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FcElectricalSensor } from "react-icons/fc";
import "../Common/Common.css";

const Navigation = () => {
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" variant="light" className="navStyle">
        <Navbar.Brand className="logo" as={Link} to="/">
          <span className="logoName">
            JMBP Web Solutions <FcElectricalSensor />
          </span>
          <span className="logoJmbp">JMBP Ltd</span>
          <br />
          <p className="logo-Paragraph">
            a team to build better website .......
          </p>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Item className="navPadding">
              <Nav.Link href as={Link} to="/">
                <span className="navBorder">
                  <span> Home </span>
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className="navPadding">
              <Nav.Link href as={Link} to="/services">
                <span className="navBorder">
                  <span>Services</span>
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className="navPadding">
              <Nav.Link href as={Link} to="/portfolio">
                <span className="navBorder">
                  <span>PORTFOLIO</span>
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className="navPadding">
              <Nav.Link href as={Link} to="/about">
                <span className="navBorder">
                  <span>About</span>
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className="navPadding">
              <Nav.Link href as={Link} to="/contact">
                <span className="navBorder">
                  <span> Contact</span>
                </span>
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
};

export default Navigation;
