import React from "react";
import { Container, Row, Col } from "react-bootstrap";
//import TrackVisibility from "react-on-screen";
import FlipInXAnimation from "../Common/AnimationStyles/FlipInXAnimation";

const RequestQuote = () => {
  return (
    <Container fluid className="request-quote">
      <Container>
        <Row>
          <Col>
            <FlipInXAnimation>
              <div className="request-quote-text">
                <span className="underline--magical">JMBP Web Solutions</span>{" "}
                creative team will satisfy you fully as per your expectation in
                delivering our all above listed services.{" "}
                <span className="underline--magical">Contact Us Today</span> and
                achieve ultimate happiness for your business!!
              </div>
              <div className="request-button">
                <span>REQUEST A QUOTE</span>
              </div>
            </FlipInXAnimation>
          </Col>
        </Row>
      </Container>
    </Container>
  );
};

export default RequestQuote;
