import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import "../Common/Common.css";
//import { FaMapMarkerAlt } from "react-icons/fa";
import {
  FcCellPhone,
  FcFeedback,
  FcOvertime,
  FcDepartment,
  FcServices,
} from "react-icons/fc";

const Footer = () => {
  var date = new Date();
  var yearNow = date.getFullYear();

  return (
    <>
      <Container fluid className="footer">
        <Container className="footer-inner">
          <Row>
            <Col md={3}>
              <div className="footer-details">
                <h2>Office:</h2>
                <span>
                  <FcDepartment />
                </span>
                <p>
                  Unit 8 Bessemer Park,
                  <br />
                  250 Milkwood Road,
                  <br />
                  London, SE24 0HG
                </p>
              </div>
            </Col>

            <Col md={3}>
              <div className="footer-details">
                <h2>Get In Touch:</h2>
                <span>
                  <FcFeedback />
                </span>
                <p>jerome@jmbp-print.co.uk</p>
                <span>
                  <FcCellPhone />
                </span>
                <p>0207 928 5765</p>
              </div>
            </Col>

            <Col md={3}>
              <div className="footer-details">
                <h2>Business Hours:</h2>
                <span>
                  <FcOvertime />
                </span>
                <p>
                  <b>Mon-Fri :</b> 9:00am - 7:00pm <br />
                  <b>Sat :</b> 9:00am - 1:00pm <br />
                  <b>Sun :</b> Closed
                </p>
              </div>
            </Col>

            <Col md={3}>
              <div className="footer-details">
                <h2>Our Services:</h2>
                <span>
                  <FcServices />
                </span>
                <p>WEBSITE DESIGN</p>
                <p>WEBSITE DEVELOPMENT</p>
                <p>ECOMMERCE SOLUTIONS</p>
                <p>MOBILE APP DEVELOPMENT</p>
                <p>WORDPRESS DEVELOPMENT</p>
                <p>SEARCH ENGINE OPTIMISATION</p>
              </div>
            </Col>
          </Row>
        </Container>
      </Container>
      <Container fluid>
        <Row>
          <Col>
            <div className="footer-copyright">
              Copyright © {yearNow} JMBP Ltd. All Rights Reserved.
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Footer;
