import React from "react";
import { Helmet } from "react-helmet";
import ServiceJumbotron from "../Components/ServicesComponents/ServiceJumbotron";
import "../Components/ServicesComponents/Services.css";
import AllServices from "../Components/ServicesComponents/AllServices";
import ServiceRequestQuote from "../Components/ServicesComponents/ServiceRequestQuote";

const ServicesPage = () => {
  return (
    <>
      <Helmet>
        <title>JMBP Web Solutions - Services</title>
      </Helmet>

      <ServiceJumbotron />
      <AllServices />
      <ServiceRequestQuote />
    </>
  );
};

export default ServicesPage;
